import React, { useState, useEffect } from "react";
import {
  Button,
  Card,
  CardHeader,
  CardBody,
  FormGroup,
  Form,
  Input,
  Container,
  Row,
  Col,
} from "reactstrap";
// core components
import UserHeader from "components/Headers/UserHeader.js";
//api
import DataService from "../../services/requestApi";
import queryString from "query-string";
import Loaders from "components/loader";
import { message } from "antd";
const Profile = () => {
  const [names, setName] = useState("");
  const [password, setPassword] = useState("");
  const [cpassword, setCPassword] = useState("");
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [mounted, setMounted] = useState(true);
  useEffect(() => {
    const userData = async () => {
      try {
        setLoading(true);
        const res = await DataService.OwnerData();
        if (mounted) {
          console.log(res.data.admin?.name);
          setName(res.data.admin?.name);
          setData(res.data?.admin);
          if (!res.data.admin.password_status) {
            message.error("Please Change Your Password after first login");
          }
          setLoading(false);
        }
      } catch (e) {
        console.log(e);
      }
    };
    userData();
    return () => {
      userData();
      setMounted(false);
    };
  }, [mounted]);
  const SubmitData = async (e) => {
    e.preventDefault();
    var Data = {
      name: names,
    };
    setLoading(true);
    if (password.length > 0) {
      Data.password = password;
      Data.password_status = true;
    }
    try {
      const res = await DataService.ChangeProfile(queryString.stringify(Data));
      if (res.data.status_code === 200) {
        sessionStorage.setItem("current_user_name", res.data.data.name);
        window.location.href = "/admin/index";
      }
      setLoading(false);
    } catch (e) {
      console.log(e);
    }
  };
  return (
    <>
      <UserHeader />
      {/* Page content */}
      <Loaders open={loading} />
      <Container className="mt--7" fluid>
        <Row className="justify-content-center">
          <Col xl="8">
            <Card className="bg-secondary shadow">
              <CardHeader className="bg-white border-0">
                <Row className="align-items-center">
                  <Col xs="12">
                    <h3 className="mb-0">My account</h3>
                  </Col>
                </Row>
              </CardHeader>
              <CardBody>
                <Form onSubmit={SubmitData}>
                  <h6 className="heading-small text-muted mb-4">Your information</h6>
                  <div className="pl-lg-4">
                    <Row>
                      {data.password_status && (
                        <Col lg="4">
                          <FormGroup>
                            <label className="form-control-label" htmlFor="input-username">
                              Name
                            </label>
                            <Input
                              className="form-control-alternative"
                              id="input-username"
                              placeholder="Username"
                              value={names}
                              onChange={(e) => setName(e.target.value)}
                              type="text"
                            />
                          </FormGroup>
                        </Col>
                      )}
                      <Col lg="4">
                        <FormGroup>
                          <label className="form-control-label" htmlFor="input-email">
                            Password
                          </label>
                          <Input
                            className="form-control-alternative"
                            id="input-email"
                            placeholder="Password"
                            type="password"
                            autoComplete="new-password"
                            value={password}
                            required
                            onChange={(e) => setPassword(e.target.value)}
                          />
                        </FormGroup>
                      </Col>
                      <Col lg="4">
                        <FormGroup>
                          <label className="form-control-label" htmlFor="input-email">
                            Confirm Password
                          </label>
                          <Input
                            className="form-control-alternative"
                            id="input-Password"
                            placeholder="Password"
                            type="password"
                            value={cpassword}
                            required
                            onChange={(e) => setCPassword(e.target.value)}
                            autoComplete="new-password"
                          />
                        </FormGroup>
                        {cpassword !== password ? "Password Mismatch" : null}
                      </Col>
                      <Col lg="12">
                        <Button className="my-4" color="primary" type="submit" block>
                          Update Profile
                        </Button>
                      </Col>
                    </Row>
                  </div>
                </Form>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Container>
    </>
  );
};

export default Profile;
