import React, { useState } from "react";
import { useHistory } from "react-router-dom";
//Api
import DataService from "../../services/requestApi";
import queryString from "query-string";
import { Button, Form, Input } from "reactstrap";
import { message } from "antd";
//socket
import { socket } from "services/socket";
const ManageWallet = ({ type, userID }) => {
  let history = useHistory();
  const [coins, setCoins] = useState("");

  const SubmitWallet = async (e) => {
    e.preventDefault();

    const Data = {
      user_id: userID,
      coins,
    };
    try {
      const res = await DataService.ManageWallets(type, queryString.stringify(Data));
      if (res.data.status_code === 200) {
        const data = {
          user_id: userID,
        };
        socket.emit("userData", data, (error) => {
          if (error) {
            alert(error);
          }
        });
        history.push("/");
      } else if (res.data.status_code === 400) {
        message.error(res.data.message);
      }
    } catch (e) {
      console.log(e);
    }
  };
  return (
    <Form className="formbox" onSubmit={SubmitWallet}>
      <Input
        id="POints"
        label="Enter Points"
        type="text"
        placeholder="Eg:- 500, 1000"
        value={coins}
        onChange={(e) => setCoins(e.target.value)}
      />
      <Button className="my-4" color="primary" type="submit" block>
        Submit
      </Button>
    </Form>
  );
};
export default ManageWallet;
