import http from "./http-common";
class DataService {
  // Login Services
  Login(data) {
    return http.get("admin/login?" + data);
  }

  OwnerData() {
    return http.get("admin/data");
  }

  // Admin dataService
  AdminAdd(data) {
    return http.post("admin/user/create", data);
  }
  AdminUpdate(id, data) {
    return http.patch("admin/user/update/" + id, data);
  }
  CheckUserExists(code) {
    return http.post("admin/check_user", code);
  }
  SuperAdminAllUser() {
    return http.get("admin/user/all");
  }
  ChangeProfile(data) {
    return http.patch("admin/update", data);
  }
  ManageWallets(type, data) {
    return http.post(`admin/${type}/user`, data);
  }
  SuperAdminTransaction() {
    return http.get("admin/transaction/all");
  }
}

export default new DataService();
