import React from "react";
import { Link } from "react-router-dom";
import { Table, Input, Label, Badge, Button } from "reactstrap";
const Alluser = ({ Alladmin, BlockUser, UpdateMode }) => {
  return (
    <>
      <Table className="align-items-center table-flush" responsive>
        <thead className="thead-light">
          <tr>
            <th scope="col">User Code</th>
            <th scope="col">Name</th>
            <th scope="col">Balance</th>
            <th scope="col">Exposure</th>
            <th scope="col">Client (P/L)</th>
            <th scope="col">Available Balance </th>
            <th scope="col">U ST</th>
            <th scope="col">B ST</th>
            <th scope="col" className="text-right">
              Action
            </th>
          </tr>
        </thead>
        <tbody>
          {Alladmin &&
            Alladmin.map((item, index) => (
              <tr key={item._id}>
                <td>#{item.code}</td>
                <td>
                  <Badge
                    className="bg-dark text-white p-2"
                    pill
                    style={{ fontSize: 11 }}
                  >
                    #{item.name}
                  </Badge>
                </td>
                <td>{item.balance}</td>
                <td>{item.exposure}</td>
                <td>{item.pl}</td>
                <td>{item.wallet}</td>
                <td>
                  <Label className="custom-toggle custom-toggle-warning">
                    <Input
                      defaultChecked={item.status}
                      type="checkbox"
                      onClick={(e) => BlockUser(e, item)}
                    />
                    <span
                      className="custom-toggle-slider rounded-circle"
                      data-label-off="Off"
                      data-label-on="On"
                    />
                  </Label>
                </td>
                <td>
                  <Label className="custom-toggle custom-toggle-warning">
                    <Input
                      defaultChecked={item.status}
                      type="checkbox"
                      onClick={(e) => BlockUser(e, item)}
                    />
                    <span
                      className="custom-toggle-slider rounded-circle"
                      data-label-off="Off"
                      data-label-on="On"
                    />
                  </Label>
                </td>
                <td>
                  <Button
                    color="dark"
                    onClick={(e) => UpdateMode(e, item)}
                    size="sm"
                  >
                    <i className="fas fa-edit" />
                  </Button>
                  <Link
                    to={{
                      pathname: "/admin/withdraw",
                      state: item,
                    }}
                    className="btn btn-dark btn-sm"
                  >
                    W
                  </Link>
                  <Link
                    to={{
                      pathname: "/admin/transfer",
                      state: item,
                    }}
                    className="btn btn-dark btn-sm"
                  >
                    D
                  </Link>
                </td>
              </tr>
            ))}
        </tbody>
      </Table>
    </>
  );
};
export default Alluser;
