import moment from "moment";
import React from "react";
import { Table } from "reactstrap";
const TransactionList = ({ data }) => {
  console.log(data);
  return (
    <>
      <Table className="align-items-center table-flush" responsive>
        <thead className="thead-light">
          <tr>
            <th scope="col">Sr. no</th>
            <th scope="col">User Code</th>
            <th scope="col">User Name</th>
            <th scope="col">Method</th>
            <th scope="col">Wallet</th>
            <th scope="col">Current Balance</th>
            <th scope="col">Date</th>
          </tr>
        </thead>
        <tbody>
          {data &&
            data.map((item, index) => (
              <tr key={item._id}>
                <th scope="row">{index + 1}. </th>
                <td># {item?.from_id?.code}</td>
                <td>{item?.from_id?.name}</td>
                <td>
                  <span
                    style={
                      item.mode === "transfer"
                        ? {
                            color: "#47c44c",
                            display: "block",
                            textTransform: "capitalize",
                          }
                        : {
                            color: "#ff4545",
                            display: "block",
                            textTransform: "capitalize",
                          }
                    }
                  >
                    {item.mode === "transfer"
                      ? item.mode + ` to ${item.to_id?.name}`
                      : item.mode + ` from your side`}
                  </span>
                </td>
                <td>
                  <span
                    style={item.mode === "transfer" ? { color: "#47c44c" } : { color: "#ff4545" }}
                  >
                    {item.mode === "transfer" ? "+" : "-"} {item.coins}
                  </span>
                </td>
                <td>
                  <td>{item.from === "admins" ? item?.from_balance : item?.to_balance}</td>
                </td>
                <td>{moment(item.created_at).format("ll")}</td>
              </tr>
            ))}
        </tbody>
      </Table>
    </>
  );
};
export default TransactionList;
