import React, { useEffect, useState, useCallback } from "react";
import { Button, Card, CardHeader, Container, Row, Col, Input, CardBody, Form } from "reactstrap";

import Header from "components/Headers/Header.js";
import CustomModal from "components/CustomModal";

//api
import DataService from "../services/requestApi";
import queryString from "query-string";
import Loaders from "components/loader";
import { message } from "antd";
import Alluser from "components/User";
import { socket } from "services/socket";
import { useHistory } from "react-router-dom";
//socket
const Index = (props) => {
  let history = useHistory();
  const [adminData, setAdminData] = useState([]);
  const [Alladmin, setAllAdmin] = useState([]);
  const [loading, setLoading] = useState(false);
  const [mounted, setMounted] = useState(true);
  const [names, setName] = useState("");
  const [code, setCode] = useState("");
  const [password, setPassword] = useState("");
  const [modal, setModal] = useState(false);
  const [updateStatus, setUpdateStatus] = useState(false);
  const [currentId, setCurrentId] = useState("");
  const [adminExistsMsg, setUserExistsMsg] = useState("");
  const [adminExists, setUserExists] = useState(false);
  const AdminId = sessionStorage.getItem("current_user_id");

  useEffect(() => {
    const GetDatass = () => {
      const DATA = {
        admin_id: AdminId,
      };
      socket.emit("adminData", DATA, (error) => {
        if (error) {
          alert(error);
        }
      });
      socket.on("adminData", (data) => {
        console.log(data);
        setAdminData(data);
        if (!data.password_status) {
          history.push("/admin/user-profile");
        }
      });
    };
    GetDatass();
    return () => {
      GetDatass();
    };
  }, []);

  const GetData = useCallback(async () => {
    setLoading(true);
    try {
      const [alladmin] = await Promise.all([DataService.SuperAdminAllUser()]);

      if (mounted) {
        setAllAdmin(alladmin.data.data);
        setLoading(false);
      }
    } catch (e) {
      console.log(e);
    }
  }, [mounted]);

  useEffect(() => {
    GetData();
    return () => {
      GetData();
      setMounted(false);
    };
  }, [GetData]);

  const ToggleModal = () => {
    setModal(!modal);
    if (!modal) {
      setName();
      setPassword();
      setCode();
      setCurrentId();
      setUpdateStatus(false);
    }
  };
  const SubmitDetails = async (e) => {
    e.preventDefault();
    try {
      ToggleModal();
      const Data = {
        name: names,
        code,
        password,
      };
      const res = await DataService.AdminAdd(queryString.stringify(Data));

      if (res.data.status_code === 400) {
        message.error(res.data.message);
      } else {
        setMounted(true);
        GetData();
      }
    } catch (e) {
      console.log(e);
    }
  };

  const UpdateMode = (e, data) => {
    ToggleModal();
    setUpdateStatus(true);
    setName(data.name);
    setCode(data.code);
    setCurrentId(data._id);
  };
  const UpdateDetails = async (e) => {
    e.preventDefault();
    try {
      var Data = {
        name: names,
        code,
      };
      if (password && password.length > 5) {
        Data.password = password;
      }
      const res = await DataService.AdminUpdate(currentId, queryString.stringify(Data));
      console.log(res.data);
      if (res.data.status_code === 400) {
        message.error(res.data.message);
      } else {
        setMounted(true);
        GetData();
      }
      ToggleModal();
    } catch (e) {
      console.log(e);
    }
  };
  const BlockUser = async (e, data) => {
    try {
      const Data = {
        status: !data.status,
      };
      const res = await DataService.AdminUpdate(data._id, queryString.stringify(Data));
      if (res.data.message === "Success") {
        window.location.reload(true);
      }
    } catch (e) {
      console.log(e);
    }
  };
  useEffect(() => {
    const CheckUserExists = async (e) => {
      try {
        const Data = {
          code,
        };
        if (!updateStatus) {
          if (code && code.length > 4) {
            const res = await DataService.CheckUserExists(queryString.stringify(Data));
            console.log(res.data);
            setUserExists(true);
            setUserExistsMsg(res.data.message);
          } else {
            setUserExists(false);
          }
        }
      } catch (e) {
        console.log(e);
      }
    };
    CheckUserExists();
  }, [code]);
  return (
    <>
      <Header show adminData={adminData} />
      {/* Page content */}
      <Container className="mt--7" fluid>
        <Row className="mt-5">
          <Col className="mb-5 mb-xl-0" xl="12">
            <Card className="shadow">
              <Loaders open={loading} />
              <CardHeader className="border-0">
                <Row className="align-items-center">
                  <div className="col">
                    <h3 className="mb-0">All User</h3>
                  </div>
                  <div className="col text-right">
                    <Button color="primary" href="#pablo" onClick={ToggleModal} size="sm">
                      Add User
                    </Button>
                  </div>
                </Row>
              </CardHeader>
              <Alluser Alladmin={Alladmin} BlockUser={BlockUser} UpdateMode={UpdateMode} />
            </Card>
          </Col>
        </Row>
      </Container>
      <CustomModal
        open={modal}
        handleClose={ToggleModal}
        title={updateStatus ? "Edit User" : "Add User"}
      >
        <Container className="mt-5">
          <Row className="justify-content-center">
            <Col xl={5} lg={4} xs={12}>
              <Card className="mb-3  border">
                <CardBody>
                  <Form
                    role="form"
                    onSubmit={updateStatus ? UpdateDetails : SubmitDetails}
                    autoComplete="new"
                  >
                    <Input
                      className="w-100 mb-3"
                      id="name"
                      placeholder="Enter Name"
                      type="text"
                      autoComplete="new-name"
                      value={names}
                      onChange={(e) => setName(e.target.value)}
                      required={!updateStatus}
                    />
                    {!updateStatus && (
                      <>
                        <Input
                          className="w-100 mb-3"
                          id="username"
                          placeholder="User Name"
                          type="text"
                          autoComplete="new-username"
                          value={code}
                          onChange={(e) => setCode(e.target.value)}
                          required={!updateStatus}
                        />
                        {adminExists && <p style={{ marginBottom: 15 }}>{adminExistsMsg} !</p>}
                      </>
                    )}
                    <Input
                      id="password"
                      placeholder="Password"
                      type="password"
                      autoComplete="new-password"
                      value={password}
                      onChange={(e) => setPassword(e.target.value)}
                      required={!updateStatus}
                    />
                    {!updateStatus ? (
                      <Button
                        className="my-4"
                        color="primary"
                        type="submit"
                        block
                        disabled={adminExistsMsg === "User code already exist" ? true : false}
                      >
                        Submit
                      </Button>
                    ) : (
                      <Button className="my-4" color="primary" type="submit" block>
                        Update
                      </Button>
                    )}
                  </Form>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </Container>
      </CustomModal>
    </>
  );
};

export default Index;
