import React, { useEffect } from "react";
import { useLocation, Route, Switch, Redirect } from "react-router-dom";
// reactstrap components
import { Container } from "reactstrap";
// core components
import AdminNavbar from "components/Navbars/AdminNavbar.js";
import AdminFooter from "components/Footers/AdminFooter.js";
import Sidebar from "components/Sidebar/Sidebar.js";

import routes from "routes.js";
import { socket } from "services/socket";

const Admin = (props) => {
  const mainContent = React.useRef(null);
  const location = useLocation();
  const [isLogedin, setIsLogedin] = React.useState(false);
  const AdminId = sessionStorage.getItem("current_user_id");
  useEffect(() => {
    const GetDatass = () => {
      const DATA = {
        admin_id: AdminId,
      };
      socket.emit("adminData", DATA, (error) => {
        if (error) {
          alert(error);
        }
      });
      socket.on("adminData", (data) => {
        console.log(data);
        if (!data.status) {
          sessionStorage.clear();
          window.location.reload(true);
        }
      });
    };
    GetDatass();
    return () => {
      GetDatass();
    };
  }, []);
  React.useEffect(() => {
    const token = sessionStorage.getItem("Authtoken");
    if (token == null) {
      setIsLogedin(true);
    }
  }, []);
  React.useEffect(() => {
    document.documentElement.scrollTop = 0;
    document.scrollingElement.scrollTop = 0;
    mainContent.current.scrollTop = 0;
  }, [location]);

  const getRoutes = (routes) => {
    return routes.map((prop, key) => {
      if (prop.layout === "/admin") {
        return <Route path={prop.layout + prop.path} component={prop.component} key={key} />;
      } else {
        return null;
      }
    });
  };

  const getBrandText = (path) => {
    for (let i = 0; i < routes.length; i++) {
      if (props.location.pathname.indexOf(routes[i].layout + routes[i].path) !== -1) {
        return routes[i].name;
      }
    }
    return "Brand";
  };
  if (isLogedin) {
    return <Redirect to="/auth/login" />;
  }
  return (
    <>
      <Sidebar
        {...props}
        routes={routes}
        logo={{
          innerLink: "/admin/index",
          imgSrc: require("../assets/img/brand/argon-react.png"),
          imgAlt: "...",
        }}
      />
      <div className="main-content" ref={mainContent}>
        <AdminNavbar {...props} brandText={getBrandText(props.location.pathname)} />
        <Switch>
          {getRoutes(routes)}
          <Redirect from="*" to="/admin/index" />
        </Switch>
        <Container fluid>
          <AdminFooter />
        </Container>
      </div>
    </>
  );
};

export default Admin;
